.priceButton {
    color: #FFFFFF;
    background: #FFC85E;
    border-radius: 79px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    line-height: 17px;
    font-weight: 700;
    font-size: 14px;
    gap: 12px;
    width: 100%;
    height: 59px;
    max-height: 59px;
    min-height: 20px;
    &:hover {
        background-color: #fad074;
    }
    &:active {
        background-color: #fcd58d;
    }
    @media (max-width: 1500px) {
        height: 40px;
        font-size: 12px;
        gap: 0;
    }
    @media (max-width: 1100px) {
        gap: 15px;
        width: 290px;
        min-height: 70px;
    }
}